<template>
  <div class="className" id="multipleBarsLines" style="height:500px"></div>
</template>

<script>
import echarts from "echarts";
export default {
  // props:{
  //   func_click
  // },
  props: ["funClick"],
  watch: {
    funClick: function(newVal, oldVal) {
      if (newVal.indicator == null) {
        return;
      } else {
        this.datas.legendBar = newVal.yearList;
        let arr = [];
        arr.push(newVal.yearVal);
        arr.push(newVal.lastYearVal);
        arr.push(newVal.beforeLastYearVal);
        this.datas.yAxis = arr;
        this.datas.lines = arr;
        let array = [];
        newVal.indicator.forEach(item => {
          array.push(item.name);
        });
        this.datas.xAxis = array;
        this.myData();
      }
    }
  },

  data() {
    return {
      // yearList:[],
      chart: null,
      seriesArr: [],
      legendArr: [],
      symbol: "%",
      datas: {
        id: "multipleBarsLines",
        title: "近三年学生达成情况趋势",
        // subTitle: "最近更新：07月18日",
        legendBar: [],
        symbol: "", //数值是否带百分号        --默认为空 ''
        // legendLine: ["折线一", "折线二", "折线三"],
        xAxis: [],
        yAxis: [],
        lines: [],
        barColor: ["#3FA7DC", "#7091C4", "#5170A2"], //柱子颜色 必填参数
        lineColor: ["#638862", "#DA8084", "#333"] // 折线颜色
      }
    };
  },
  mounted() {
    this.$nextTick(() => {});
  },
  methods: {
    func_click5() {
      this.datas.legendBar = this.funClick.yearList;
      this.myData();
    },
    myData() {
      const self = this;
      let yAxis = self.datas.yAxis || [];
      let lines = self.datas.lines || [];
      let legendBar = self.datas.legendBar || [];
      let legendLine = self.datas.legendLine || [];
      var symbol = self.datas.symbol || " ";
      yAxis &&
        yAxis.forEach((item, index) => {
          this.legendArr.push({
            name: legendBar && legendBar.length > 0 && legendBar[index]
          });
          this.seriesArr.push({
            name: legendBar && legendBar.length > 0 && legendBar[index],
            type: "bar",
            barGap: "1px",
            data: item,
            barWidth: self.datas.barWidth || 12,
            label: {
              normal: {
                show: true,
                formatter: "{c}" + symbol,
                position: "top",
                textStyle: {
                  color: "#414957",
                  fontStyle: "normal",
                  fontFamily: "微软雅黑",
                  textAlign: "left",
                  fontSize: 14
                }
              }
            },
            itemStyle: {
              //图形样式
              normal: {
                barBorderRadius: 4,
                color: self.datas.barColor[index]
              }
            }
          });
        });

      lines &&
        lines.forEach((item, index) => {
          this.legendArr.push({
            name: legendLine && legendLine.length > 0 && legendLine[index]
          });
          this.seriesArr.push({
            name: legendLine && legendLine.length > 0 && legendLine[index],
            type: "line",
            data: item,
            itemStyle: {
              normal: {
                color: self.datas.lineColor[index],
                lineStyle: {
                  width: 3,
                  type: "solid"
                }
              }
            },
            label: {
              normal: {
                show: false, //折线上方label控制显示隐藏
                position: "top"
              }
            },
            symbol: "circle",
            symbolSize: 10
          });
        });
      this.initChart();
    },

    initChart() {
      var self = this;
      this.chart = echarts.init(document.getElementById("multipleBarsLines"));
      var bbb = {
        backgroundColor: "#fff",
        title: {
          show: true,
          text: self.datas.title,
          subtext: self.datas.subTitle,
          link: ""
        },
       toolbox: {
      　　show: true,
      　　feature: {
      　　　　saveAsImage: {
      　　　　show:true,
      　　　　excludeComponents :['toolbox'],
      　　　　pixelRatio: 2
      　　　　}
      　　}
        },
        tooltip: {
          trigger: "axis",
          formatter: function(params) {
            var time = "";
            var str = "";
            for (var i of params) {
              time = i.name.replace(/\n/g, "") + "<br/>";
              if (i.data == "null" || i.data == null) {
                str += i.seriesName + "：无数据" + "<br/>";
              } else {
                str +=
                  i.seriesName + "：" + i.data + self.datas.symbol + "%<br/>";
              }
            }
            return time + str;
          },
          axisPointer: {
            type: "none"
          }
        },
        legend: {
          right: this.datas.legendRight || "30%",
          top: 12,
          itemGap: 16,
          itemWidth: 10,
          itemHeight: 10,
          data: this.legendArr,
          textStyle: {
            color: "#414957",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          }
        },
        grid: {
          x: 30,
          y: 80,
          x2: 30,
          y2: 60
        },
        xAxis: {
          type: "category",
          data: this.datas.xAxis,
          axisTick: {
            show: false
          },

          axisLine: {
            show: false
          },
          axisLabel: {
            show: true,
            interval: "0",
            textStyle: {
              lineHeight: 16,
              padding: [2, 2, 0, 2],
              height: 50,
              fontSize: 12
            },
            rich: {
              Sunny: {
                height: 50,
                // width: 60,
                padding: [0, 5, 0, 5],
                align: "center"
              }
            },
            formatter: function(params, index) {
              var newParamsName = "";
              var splitNumber = 5;
              var paramsNameNumber = params && params.length;
              if (paramsNameNumber && paramsNameNumber <= 4) {
                splitNumber = 4;
              } else if (paramsNameNumber >= 5 && paramsNameNumber <= 7) {
                splitNumber = 4;
              } else if (paramsNameNumber >= 8 && paramsNameNumber <= 9) {
                splitNumber = 5;
              } else if (paramsNameNumber >= 10 && paramsNameNumber <= 14) {
                splitNumber = 5;
              } else {
                params = params && params.slice(0, 15);
              }

              var provideNumber = splitNumber; //一行显示几个字
              var rowNumber = Math.ceil(paramsNameNumber / provideNumber) || 0;
              if (paramsNameNumber > provideNumber) {
                for (var p = 0; p < rowNumber; p++) {
                  var tempStr = "";
                  var start = p * provideNumber;
                  var end = start + provideNumber;
                  if (p == rowNumber - 1) {
                    tempStr = params.substring(start, paramsNameNumber);
                  } else {
                    tempStr = params.substring(start, end) + "\n";
                  }
                  newParamsName += tempStr;
                }
              } else {
                newParamsName = params;
              }
              params = newParamsName;
              return "{Sunny|" + params + "}";
            },
            color: "#687284"
          }
        },
        yAxis: {
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          },
          axisLabel: {
            show: false
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "#F1F3F5",
              type: "solid"
            },
            interval: 3
          },
          splitNumber: 4
        },
        series: this.seriesArr
      };
      this.chart.setOption(bbb);
    }
  }
};
</script>



