<template>
  <div class="DegreeAchievement padding students">
    <!-- 达成度计算 -->
   <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/Calculate/DegreeAchievement' }">达成度计算分析</el-breadcrumb-item>
        <el-breadcrumb-item>
          达成度分析
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="DegreeAchievement-dv2">
      <span></span>
      <em class="DegreeAchievement-em">达成度分析</em>
    </div>
    <!-- <div class="DegreeAchievement-dv3">
      <el-button type="primary" round >导出数据 </el-button>
    </div> -->
    <div class="DegreeAchievement-dv4">
      <ul>
       <li @click="jump(index)" ref="menuItem" v-for="(item, index) in lilist" :key="index" :class="activeClass == index ? 'bor':''">{{item}}</li>
        <!-- <li @click="jump1" ref="menuItem1">班级达成度</li>
        <li @click="jump2" ref="menuItem2">学生达成度</li> -->
      </ul>
    </div>
    <div class="dv">
      <div class="DegreeAchievement-dv5" :span="24" style="width:100%">    
        <el-col :span="4.5">
          <div style="float: left;">
            <span>年级：</span>
            <el-select v-model="value" placeholder="请选择年级" @change="claims">
            <el-option
              v-for="item in cities"
              :key="item"
              :label="item"
              :value="item"
            >
            </el-option>
          </el-select>&nbsp;&nbsp;
          </div>
        </el-col >
        <el-col :span="5.5">
          <div style="float: left;">
             <span>毕业要求：</span>
            <el-select v-model="value1" placeholder="请先选择年级" @change="points">
            <el-option
              v-for="item in cities1"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>&nbsp;&nbsp;
          </div>
        </el-col >
        <el-col :span="6.5">
          <div style="float: left;">
             <span>毕业要求指标点：</span>
            <el-select v-model="value2" placeholder="指标点1-3">
              <el-option
                v-for="item in cities2"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
          </div>
          </el-col >
      </div>
      <br>
      <br>
      <br>
      <br>
      <div class="DegreeAchievement-dv6">
        
          <div style="float: left;">
            <span>课程分类：</span>
          <el-select v-model="value3" placeholder="分类名称" @change="classify">
            <el-option
              v-for="item in cities3"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>&nbsp;&nbsp;
          </div>
        &nbsp;&nbsp;&nbsp;
        
          <div style="float: left;">
              <span>课程名称：</span>
          <el-select v-model="value4" filterable placeholder="请先选择课程分类">
            <el-option
              v-for="item in cities4"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>&nbsp;&nbsp;
          </div>
          <div style="float: left;">
            <span v-show="falge1">班级：</span>
          <el-select v-model="value5" placeholder="班级名称" v-show="falge1">
            <el-option
              v-for="item in cities5"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>&nbsp;&nbsp;
          </div>
          <div style="float: left;">
            <span v-show="falge">学生：</span>
          <el-select v-model="value6" placeholder="姓名" filterable  v-show="falge">
            <el-option
              v-for="item in cities6"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            >
            </el-option>
          </el-select>
     
          </div>
      </div>
      <div class="classManagement-btn">
        <el-button type="primary" @click="calculate">计算结果</el-button>
      </div>
    </div>
    <div v-show="show">
        <img src="@/assets/studentLearning.png" alt="" style="width:33%; margin-right: 141px;">
        <div style="margin: 0 auto;width: 39%;margin-top: -23px;">
           <span style="display: block;font-size: 20px; color: #666;text-align: left;">1、选择、年级、查询十二项指标点</span>
        <span style="display: block;font-size: 20px; color: #666;text-align: left;">2、选择、年级、毕业要求、查询该毕业要求下的所有达成度分析</span>
        <span style="display: block;font-size: 20px; color: #666;text-align: left;">3、选择、年级、毕业要求、毕业要求指标点、查询当前指标点的信息</span>
        <span style="display: block;font-size: 20px; color: #666;text-align: left;">4、选择、年级、课程分类、课程名称、查询该课程信息</span>
        </div>
    </div>
    <div class="DegreeAchievement-dv7" v-show="falg">
        <!-- 近三年达成度趋势分析 -->
    </div>
    <div class="">
      <!--   下期  在做 -->
        <!-- <echarts :fun-click="func_click" v-show="falg"></echarts> -->
    </div>
    <div class="DegreeAchievement-dv7" v-show="falg1">
        毕业要求达成度分析
    </div>
    <div class="" style="background: #fff">
        <echarts1 :targets = target v-show="falg1"></echarts1>
    </div>
    <div class="DegreeAchievement-dv7" v-show="falg2">
        毕业要求 {{pointNames}} 达成度情况分析
    </div>
    <div class="" style="background: #fff">
        <echarts2 :pointLists = pointList v-show="falg2"></echarts2>
    </div>
    <div class="DegreeAchievement-dv7" v-show="falg3">
        {{courseName}}课程达成度
    </div>
    <div class="">
        <echarts3 :xinxi = xin v-show="falg3"></echarts3>
    </div>
  </div>
</template>
<script>
import echarts from "@/components/echaars/echars"
import echarts1 from "@/components/echaars/echars1"
import echarts2 from "@/components/echaars/echars2"
import echarts3 from "@/components/echaars/echars3"

export default {
  components:{
    echarts,
    echarts1,
    echarts2,
    echarts3
  },
  data() {
    return {
      show:true,
      falg:false,
      falg1:false,
      falg2:false,
      falg3:false,
      falge:false,
      falge1:false,
      cities: [],
      value: "",
      cities1: [],
      value1: "",
      cities2: [],
      value2: "",
      cities3: [],
      value3: "",
      cities4: [],
      value4: "",
      cities5: [],
      value5: "",
      cities6:[],
      value6: "",
      str:'',
      func_click:{},
      xin:{},
      pointList:[],
      target:{},
      lilist:[
        '专业达成度','班级达成度','学生达成度'
      ],
      activeClass: 0,
      pointNames:'',
      courseName:''
    }
  },
    methods:{
    ///basic/version/grades 
    grades(){
      this.$get('/basic/achieve/grades').then(res=>{
          this.cities = res.data
      })
    },
    // /basic/achieve/claims/{grade}
    claims(){
      this.str = this.value
      this.$get(`/basic/achieve/claims/${this.value}`).then(res=>{
          this.cities1 = res.data
          this.cities1.unshift({id:'',name:'全部'})
      })
      this.courseClass()
    },
    //   /basic/achieve/points/{claimID}
    points(){
        this.$get(`/basic/achieve/points/${this.value1}`).then(res=>{
          this.cities2 = res.data
          this.cities2.unshift({id:'',name:'全部'})
      })
    },
    // /basic/courseClassification/all
    courseClass(){
       this.$get('/basic/courseClassification/all').then(res=>{
           this.cities3 = res.data
           this.cities3.unshift({id:'',name:'全部'})
       })
    },
    //   /basic/achieve/courses/grade/{grade}/classify/{classify}
    classify(){
        this.$get(`/basic/achieve/courses/grade/${this.str}/classify/${this.value3}`).then(res=>{
              this.cities4 = res.data
              this.cities4.unshift({id:'',name:'全部'})
        })  
    },
    //   /basic/achieve/allClass
    allClass(){
      this.$get('/basic/achieve/allClass').then(res=>{
          this.cities5 = res.data
           this.cities5.unshift({id:'',name:'全部'})
      })
    },
    // /basic/achieve/allStudent
    allStudent(){
      this.$get('/basic/achieve/allStudent').then(res=>{
          this.cities6 = res.data
           this.cities6.unshift({id:'',name:'全部'})
      })
    },
    // /basic/achieve/calculate/show
    calculate(){
      let key = {
        grade:this.value,
        claimID:this.value1,
        pointID:this.value2,
        courseID:this.value4,
        classID:this.value5,
        studentID:this.value6
      }
      console.log(key,'key')
      if(key.claimID == '' && key.pointID == '' && key.courseID == ''  && key.studentID== '' && key.grade !== '' && key.classID== ''){
          this.falg = true
          this.falg1 = true
          this.show = false
      }else if(key.claimID !== ''  && key.grade !== ''&& key.courseID !== ''){
          this.falg = false
          this.falg1 = false
          this.falg2 = true
          this.falg3 = true
          this.show = false
      }else if(key.claimID !== ''  && key.grade !== ''){
          this.falg = false
          this.falg1 = false
          this.falg2 = true
          this.falg3 = false
          this.show = false
      }else if(key.claimID !== ''  && key.grade !== '' && key.pointID !== ''&& key.courseID !== ''){
          this.falg = false
          this.falg1 = false
          this.falg2 = true
          this.falg3 = true
          this.show = false
      }else if(key.grade !== '' && key.courseID !== '' ) {
          this.falg = false
          this.falg1 = false
          this.falg2 = false
          this.falg3 = true
          this.show = false
      }
      this.$post('/basic/achieve/analysis',key).then(res=>{
        console.log(res,'计算结果')
        if(res.status == 1){
          if(res.data.claimAchieve == null){
              this.func_click = null
          }else{
            this.func_click = res.data.claimAchieve
          }
          
          if(res.data.courseAchieve == null){
            this.xin = null
          }else{
            this.xin = res.data.courseAchieve
            this.courseName = this.xin.courseName
          }
          //  this.pointList = res.data.pointAchieveList
          if(key.pointID !== ''){
            this.pointList = res.data.pointAchieveList
          }else{
            this.pointList = res.data.pointAchieveList
          }
          this.target = res.data.claimAchieve
          let pointName = []
          this.pointList.forEach(ite=>{
            if( this.pointList.length>1){
              
            }else{
              this.pointNames = ite.pointName
            }
          })
        }
      })
    },
    jump(index){
      this.activeClass = index
      if(index == 0){
        this.falge = false
        this.falge1 = false
      }else if(index == 1){
        this.falge = false
        this.falge1 = true
      }else if(index == 2){
        this.falge = true
        this.falge1 = true
      } 
    },
  },
  mounted(){
    this.grades()
    this.allClass()
    this.allStudent()
  }
};
</script>
<style lang="scss" scoped>
.DegreeAchievement {
  min-width: 980px;
    overflow: auto;
  .bor{
    background-color: rgb(50, 104, 247);
    color: #fff;
  }
  .DegreeAchievement-dv2 {
    margin-top: 18px;
    // float: left;
    text-align: left;
    margin-bottom: 13px;
    span {
      width: 2px;
      height: 14px;
      display: inline-block;
      background-color: #0606ff;
    }
    .DegreeAchievement-em {
      font-style: normal;
      padding-top: 8px;
    }
  }
  .DegreeAchievement-dv3 {
    margin-top: 18px;
    margin-bottom: 13px;
  }
  .DegreeAchievement-dv4 {
    ul {
      overflow: hidden;
      background-color: rgb(238, 238, 238);
      li {
        float: left;
        list-style: none;
        padding-right: 25px;
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 4px;
      }
      li:nth-child(2){
          padding-left: 25px;
          border-left: 1px solid #ccc;
      }
      li:nth-child(3){
          padding-left: 25px;
          border-left: 1px solid #ccc;
      }
    }
  }
  .DegreeAchievement-dv5 {
    margin-top: 18px;
    float: left;
    span{
      font-size: 0.9rem;
    }
  }
  .DegreeAchievement-dv6 {
    float: left;
    // margin-top: 10px;
    span{
      font-size: 0.9rem;
    }
  }
  .DegreeAchievement-dv7{
      text-align: left;
      margin-top: 18px;
      margin-bottom: 20px;
  }
  .dv{
      margin-bottom: 60px;
      overflow: hidden;
      position: relative;
      .classManagement-btn {
          // position: absolute;
          // right: 18%;
          // top: 40%;
          float: right;
    }
  }

}
</style>



